import { Component, OnInit } from '@angular/core'
import * as Reducers from 'src/app/store/reducers'
import {
    slideFadeinUp,
    slideFadeinRight,
    zoomFadein,
    fadein,
} from 'src/app/layouts/App/router-animations'
import { Observable } from 'rxjs'
import { Store, select } from '@ngrx/store'
import { MenuService } from 'src/app/services/menu.service'
import { RouterOutlet } from '@angular/router'

@Component({
    selector: 'app-purchasing',
    templateUrl: './purchasing.component.html',
    styleUrls: ['./purchasing.component.scss'],
    animations: [slideFadeinUp, slideFadeinRight, zoomFadein, fadein],
})
export class PurchasingComponent implements OnInit {
    settings$: Observable<any>
    menuLayoutType: string
    isContentNoMaxWidth: boolean
    isAppMaxWidth: boolean
    isGrayBackground: boolean
    isSquaredBorders: boolean
    isCardShadow: boolean
    isBorderless: boolean
    isTopbarFixed: boolean
    isGrayTopbar: boolean
    routerAnimation: string

    constructor(private store: Store<any>, private menuService: MenuService) {
        this.store.pipe(select(Reducers.getSettings)).subscribe(state => {
            this.menuLayoutType = state.menuLayoutType
            this.isContentNoMaxWidth = state.isContentNoMaxWidth
            this.isAppMaxWidth = state.isAppMaxWidth
            this.isGrayBackground = state.isGrayBackground
            this.isSquaredBorders = state.isSquaredBorders
            this.isCardShadow = state.isCardShadow
            this.isBorderless = state.isBorderless
            this.isTopbarFixed = state.isTopbarFixed
            this.isGrayTopbar = state.isGrayTopbar
            this.routerAnimation = state.routerAnimation
        })
    }

    menuData: any = []
    ngOnInit() {
        this.menuData = [
            {
                title: 'Purchase Request',
                key: 'pr',
                icon: 'fe fe-home',
                count: 6,
                children: [
                    {
                        title: 'All Data',
                        key: 'pr/request',
                        icon: 'fe fe-bookmark',
                        url: 'pr/request',
                    },
                    {
                        title: 'All Item',
                        key: 'pr/list-item',
                        icon: 'fe fe-bookmark',
                        url: 'pr/list-item',
                    }
                ],
            },
            {
                title: 'Purchase Order',
                key: 'po',
                icon: 'fe fe-home',
                count: 6,
                children: [
                    {
                        title: 'All Data',
                        key: 'po/order',
                        icon: 'fe fe-bookmark',
                        url: 'po/order',
                    },
                    {
                        title: 'All Item',
                        key: 'po/list-item-po',
                        icon: 'fe fe-bookmark',
                        url: 'po/list-item-po',
                    }
                ],
            },
            {
                title: 'Received letter',
                key: 'spb',
                icon: 'fe fe-home',
                count: 6,
                children: [
                    {
                        title: 'All Data',
                        key: 'spb/penerimaan-barang',
                        icon: 'fe fe-bookmark',
                        url: 'spb/penerimaan-barang',
                    },
                    {
                        title: 'All Item',
                        key: 'spb/list-spb',
                        icon: 'fe fe-bookmark',
                        url: 'spb/list-spb',
                    }
                ],
            },
            {
                title: 'Work Order',
                key: 'wo',
                icon: 'fe fe-home',
                count: 6,
                children: [
                    {
                        title: 'All Data',
                        key: 'wo/repair',
                        icon: 'fe fe-bookmark',
                        url: 'work-order/list',
                    },
                    {
                        title: 'All Item',
                        key: 'wo/list-item',
                        icon: 'fe fe-bookmark',
                        url: 'work-order-item/list',
                    }
                ],
            },
            {
                title: 'Received',
                key: 'Received',
                icon: 'fe fe-home',
                count: 6,
                children: [
                    {
                        title: 'All Data',
                        key: 'Received/repair',
                        icon: 'fe fe-bookmark',
                        url: 'received/list',
                    },
                    {
                        title: 'All Item',
                        key: 'Received/list-item',
                        icon: 'fe fe-bookmark',
                        url: 'received-item/list',
                    }
                ],
            },
            {
                title: 'Return',
                key: 'Received',
                icon: 'fe fe-home',
                count: 6,
                children: [
                    {
                        title: 'All Data',
                        key: 'return/repair',
                        icon: 'fe fe-bookmark',
                        url: 'return/list',
                    },
                    {
                        title: 'All Item',
                        key: 'return-item/repair',
                        icon: 'fe fe-bookmark',
                        url: 'return-item/list',
                    }
                ],
            },



            {
                title: 'Supplier',
                key: 'suplier',
                icon: 'fe fe-home',
                count: 6,
                children: [
                    {
                        title: 'Contact',
                        key: 'suplier/contact',
                        icon: 'fe fe-bookmark',
                        url: '../asset/supplier/contact',
                    },
                    {
                        title: 'Manufacture',
                        key: 'supplier/manufacture',
                        icon: 'fe fe-bookmark',
                        url: '../asset/supplier/manufacture',
                    },
                    {
                        title: 'Vendor',
                        key: 'vendor',
                        icon: 'fe fe-bookmark',
                        url: '../asset/supplier/vendor',
                    },
                ],
            },
        ]

        this.menuService.menu.next(this.menuData)
    }

    routeAnimation(outlet: RouterOutlet, animation: string) {
        if (animation === this.routerAnimation) {
            return outlet.isActivated && outlet.activatedRoute.routeConfig.path
        }
    }
}
