import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'

import { PurchasingRoutingModule } from './purchasing-routing.module'
import { PurchasingComponent } from './purchasing.component'
import { ApiModule } from 'src/app/sdk/asset/api.module'
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar'
import { WidgetsComponentsModule } from 'src/app/components/widgets/widgets-components.module'
import { NzLayoutModule, NgZorroAntdModule } from 'ng-zorro-antd'
import { LayoutModule } from 'src/app/components/layout/layout.module'
import { FormsModule } from '@angular/forms'
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper'
import { ApiModule as ApiModuleHr } from 'src/app/sdk/hr/api.module';
import { ApiModule as ApiModuleUser } from 'src/app/sdk/user/api.module';
import { ApiModule as ApiModulePurchasing } from 'src/app/sdk/purchasing/api.module';
import { ListItemComponent } from './pr/list-item/list-item.component';
import { ListItemPoComponent } from './po/list-item-po/list-item-po.component';
import { ListSpbComponent } from './spb/list-spb/list-spb.component'
import { HTTP_INTERCEPTORS } from '@angular/common/http'
import { JwtInterceptor } from 'src/app/_role/jwt.interceptor'
import { ErrorInterceptor } from 'src/app/_role/error.interceptor'

@NgModule({
    declarations: [PurchasingComponent, ListItemComponent, ListItemPoComponent, ListSpbComponent],
    imports: [
        CommonModule,
        PurchasingRoutingModule,
        ApiModule.forRoot({ rootUrl: 'http://erp-asset.vm.devetek.com/api' }),
        ApiModuleHr.forRoot({ rootUrl: 'http://erp-hr.vm.devetek.com/api' }),
        ApiModulePurchasing.forRoot({ rootUrl: 'http://erp-purchasing.vm.devetek.com/api' }),
        ApiModuleUser.forRoot({ rootUrl: 'http://erp-user.vm.devetek.com/api' }),
        PerfectScrollbarModule,
        WidgetsComponentsModule,
        NzLayoutModule,
        LayoutModule,
        NgZorroAntdModule,
        FormsModule,
    ],
    providers: [
        { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
        { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
        FormBuilderTypeSafe
    ],


})
export class PurchasingModule { }
