import { Component, OnInit } from '@angular/core';
import { PurchasingPurchaseOrderItemService } from 'src/app/sdk/purchasing/services';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-list-item-po',
    templateUrl: './list-item-po.component.html',
    styleUrls: ['./list-item-po.component.scss']
})
export class ListItemPoComponent implements OnInit {

    constructor(
        private purchasingPurchaseOrderItemService: PurchasingPurchaseOrderItemService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    ngOnInit() {
        this.searchData()
    }

    pageIndex = 1;
    pageSize = 10;
    total = 1;
    listOfData = [];
    loading = true;
    sortValue: string | null = null;
    sortKey: string | null = null;
    filterGender = [{ text: 'male', value: 'male' }, { text: 'female', value: 'female' }];
    search: string = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.searchData();
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1;
        }
        this.loading = true;
        this.purchasingPurchaseOrderItemService
            .getPurchasingPurchaseOrderItems(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey!, sortValue: this.sortValue!, search: this.search
            }))
            .subscribe((data: any) => {
                this.loading = false;
                this.total = data.data.total;
                this.listOfData = data.data.data;
            });
    }

    updateFilter(value: string): void {
        this.search = value;
        this.searchData(true);
    }

}
