import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'
import { PurchasingComponent } from './purchasing.component'
import { ListItemComponent } from './pr/list-item/list-item.component'
import { ListItemPoComponent } from './po/list-item-po/list-item-po.component'
import { ListSpbComponent } from './spb/list-spb/list-spb.component'

const routes: Routes = [
    {
        path: '',
        component: PurchasingComponent,
        children: [
            {
                path: 'pr/request',
                loadChildren: () =>
                    import('./pr/request/request.module').then(mo => mo.RequestModule),

            },
            {
                path: 'pr/list-item',
                component:ListItemComponent
            },
            {
                path: 'po/order',
                loadChildren: () =>
                    import('./po/order/order.module').then(mo => mo.OrderModule),

            },
            {
                path: 'po/list-item-po',
                component:ListItemPoComponent
            },
            {
                path: 'spb/penerimaan-barang',
                loadChildren: () =>
                    import('./spb/penerimaan-barang/penerimaan-barang.module').then(mo => mo.PenerimaanBarangModule),

            },
            {
                path: 'spb/list-spb',
                component:ListSpbComponent
            }
        ],
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class PurchasingRoutingModule {}
